import React, { Component } from 'react';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import Badge from '@mui/material/Badge';
import { Box } from '@mui/system';
import Medal from './Medal';

class Country extends Component {
  getMedalsTotal(country, medals) {
    let sum = 0;
    medals.forEach(medal => { sum += country[medal.name]; });
    return sum;
  }

  render() { 
    const { country, medals, onAdd, onSub } = this.props;
    return (
      <Paper elevation={3} sx={{ width: 260, p: 1, m: 'auto', mt: 1 }}>
        <Box sx={{ pt: 1, fontSize: 20, fontWeight: 'bold' }}>
          <Badge color="primary" badgeContent={ this.getMedalsTotal(country, medals) }>
            <Box sx={{ pr: 1 }}>{country.name}</Box>
          </Badge>
        </Box>
        <Divider sx={{ m: 1 }} />
        { medals.map(medal => 
          <Medal 
            key={ medal.id } 
            country={ country } 
            medal={ medal } 
            onAdd={ onAdd } 
            onSub={ onSub } />
        )}
      </Paper>
    );
  }
}
 
export default Country;