import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import React, { Component } from 'react';
import Badge from '@mui/material/Badge';
import { Box } from '@mui/system';
import './App.css';
import Country from './components/Country';

class App extends Component {
  state ={
    countries: [
      { id: 1, name: 'United States', gold: 2, silver: 2, bronze: 3 },
      { id: 2, name: 'China', gold: 3, silver: 1, bronze: 0 },
      { id: 3, name: 'Germany', gold: 0, silver: 2, bronze: 2 },
    ],
    medals: [
      { id: 1, name: 'gold', RGBColor: '#ffc107' },
      { id: 2, name: 'silver', RGBColor: '#c4cacc' },
      { id: 3, name: 'bronze', RGBColor: '#cd7f32' },
    ]
  }

  handleAdd = (countryId, medalName) => {
    var countriesCopy = this.state.countries;
    const countryIdx = countriesCopy.findIndex((c) => c.id === countryId);
    countriesCopy[countryIdx][medalName] += 1;
    this.setState({ countries: countriesCopy });
  }
  
  handleSub = (countryId, medalName) => {
    var countriesCopy = this.state.countries;
    const countryIdx = countriesCopy.findIndex((c) => c.id === countryId);
    countriesCopy[countryIdx][medalName] -= 1;
    this.setState({ countries: countriesCopy });
  }
  
  getAllMedalsTotal() {
    let sum = 0;
    this.state.medals.forEach(medal => { sum += this.state.countries.reduce((a, b) => a + b[medal.name], 0); });
    return sum;
  }

  render() { 
    return (
      <React.Fragment>
        <Box sx={{ textAlign: 'center', m: 0, bgcolor: '#BBBBF1', position: 'fixed', width: '100%', pt: '15px', pb: '10px', zIndex: '1000', opacity: '.95' }}>
          <Badge 
            sx={{ fontSize: '2.0em' }}
            badgeContent={ this.getAllMedalsTotal() } 
            color='primary'>
            <Box sx={{ pr: '8px' }}>Olympic Medals</Box>
          </Badge>
        </Box>
        <Container sx={{ pt: '80px', pb: '20px'  }}>
          <Grid container spacing={1} justifyContent='center'>
            {this.state.countries.map(country => 
              <Country
                key={country.id}
                country={country}
                medals={ this.state.medals }
                onAdd={this.handleAdd}
                onSub = {this.handleSub}
              />
            )}
          </Grid>
        </Container>
      </React.Fragment>
    );
  }
}

export default App;
